
.body{
    width:100%;
    height:100vh;  
}
.navbar{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.nav{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
    background-color: white;
    margin: 10px;
    border-radius: 20px;
}
.nav button{
    color:white;
    border: none;
    padding:15px;
    width:100px;
}
